.switch{
    position: relative;
}
.switch-checkbox {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    border: 0;
    appearance: none;
}
.switch-container {
    position: relative;
    width: 48px;
    height: 28px;
    border-radius: 28px;
    background: #a7aaa6;
    z-index: 0;
    margin: 0;
    padding: 0;
    appearance: none;
    border: 0;
    transition: all 0.3s;
}
.switch-container::after {
    position: absolute;
    left: 1px;
    top: 1px;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    background-color: #fff;
    content: "";
    transition: all 0.3s;
}
.switch-container-checked {
    background-color: #4dd865;
}
.switch-container-checked::after {
    transform: translateX(20px);
}