.App {
  text-align: center;
  font-family: Source Han Sans CN;
}

.container{
  display: flex;
  width: 60%;
  height: 800px;
  align-items: center;
  justify-content: center;
  margin:auto auto;
}

.title{
  font-weight: 600;
  font-size: 18px;
}

.close_img{
  width: 20px;
  height: 20px;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.slider-bar-wrap {
  margin: 20px auto;
  display: flex;
  justify-content: center;
}

.slider-bar-box {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.slider-bar-item {
  height: 50px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  color: #181818;
  letter-spacing: 0;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
}

.slider-bar-active {
  color: #FF6A00;
}

.active-line {
  width: 71px;
  height: 2px;
  background: #FF6A00;
  position: absolute;
  bottom: 0;
  transition: all 0.3s ease;
}

#snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: white;
  border:1px solid #777;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  top: 200px;
  height: 10px;
  font-size: 17px;
  line-height: 10px;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}